<template>
  <el-container class="home-container">
    <el-header>
      <div>
        <img src="../assets/logo.png" alt width="30px" />
        <span>新冠病毒报告生成系统</span>
      </div>
    </el-header>
    <el-container>
      <el-aside :width="isCollapse?'64px':'200px'">
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <el-menu
          :default-active="activePath"
          class="el-menu-vertical-demo"
          background-color="#373744"
          text-color="#fff"
          active-text-color="#409eff"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          :router="true"
        >
          <!--一级菜单-->
          <el-submenu index="1">
            <template slot="title">
              <!--图标-->
              <i class="el-icon-s-cooperation"></i>
              <!--文本-->
              <span>医院</span>
            </template>
            <!--二级菜单-->
            <el-menu-item index="/hospitals" @click="saveNavState('/hospitals')">
              <template slot="title">
                <!--图标-->
                <i class="el-icon-menu"></i>
                <!--文本-->
                <span>医院列表</span>
              </template>
            </el-menu-item>
            <el-menu-item index="/departments" @click="saveNavState('/departments')">
              <template slot="title">
                <!--图标-->
                <i class="el-icon-menu"></i>
                <!--文本-->
                <span>科室列表</span>
              </template>
            </el-menu-item>
            <el-menu-item index="/doctors" @click="saveNavState('/doctors')">
              <template slot="title">
                <!--图标-->
                <i class="el-icon-user-solid"></i>
                <!--文本-->
                <span>医生列表</span>
              </template>
            </el-menu-item>
          </el-submenu>
          <!--一级菜单-->
          <el-submenu index="2">
            <template slot="title">
              <!--图标-->
              <i class="el-icon-user-solid"></i>
              <!--文本-->
              <span>检测</span>
            </template>
            <!--二级菜单-->
            <el-menu-item index="/specimen" @click="saveNavState('/specimen')">
              <template slot="title">
                <!--图标-->
                <i class="el-icon-menu"></i>
                <!--文本-->
                <span>样本列表</span>
              </template>
            </el-menu-item>
            <!--
            <el-menu-item index="/report" @click="saveNavState('/report')">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>报告列表</span>
              </template>
            </el-menu-item>
            <el-menu-item index="/managers" @click="saveNavState('/managers')">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>负责人列表</span>
              </template>
            </el-menu-item>
            -->
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: false,
      activePath: ''
    }
  },
  methods: {
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // 保存链接的激活状态
    saveNavState(activePath) {
      console.log('activePath')
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    }
  },
  created() {
    this.activePath = window.sessionStorage.getItem('activePath')
  }
}
</script>

<style lang="less" scoped>
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  color: #fff;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #373744;
  .el-menu {
    border-right: none;
  }
}
.el-main {
  background-color: #eaedf1;
}
.home-container {
  height: 100%;
}
.toggle-button {
  background-color: #4a5064;
  font-size: 15px;
  line-height: 30px;
  color: white;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>
